@use 'variables' as v;
@use 'mixins' as m;
@use 'sass:color';

/* ======================================================
   ACCIONES
====================================================== */

.actions {
  display: grid;
  justify-items: center;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 4rem;

  .create-btn,
  .search-btn {
    @include m.boton(v.$blanco, v.$fuente-principal);
    border: 1px solid v.$azul;
    border-radius: 5%;
    text-transform: uppercase;

    &:hover {
      background-color: color.adjust(v.$azul, $lightness: -10%);
      color: v.$blanco;
      cursor: pointer;
      box-shadow: 1px 3px 11px 1px rgba(0, 0, 0, 0.82);
    }
  }
}

.btn--locked {
  opacity: 0.5;

  &:hover {
    background-color: inherit;
    color: inherit;
    box-shadow: none;
    cursor: not-allowed;
  }
}

.shadowbox {
  box-shadow: 0 0 14px -1px rgba(0, 0, 0, 0.75);
  @include m.contenedor;
}

.recent-notes__title {
  text-align: center;
  margin: 3rem 0;
}

.guideTitle {
  text-align: center;
  margin: 3rem 0;
}

/* ======================================================
   üß≠ GU√çA (BEM CORRECTO)
====================================================== */

.guide-content {
  max-width: 800px;
  margin: 2rem auto;
  text-align: center;
  position: relative;
  border-radius: 1rem;

  /* ===== STEP ===== */
  &__step {
    display: none;

    &--active {
      display: block;
    }
  }

  &__title {
    margin-bottom: 1rem;
    min-height: 2.5rem;
  }

  /* üñºÔ∏è MEDIA ESTABLE */
  &__media {
    width: 100%;
    max-width: 700px;
    height: 400px;
    margin: 0 auto;
    border-radius: 1rem;
    cursor: zoom-in;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    background: v.$blanco;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      transition: transform 0.3s ease;
      display: block;
    }

    &:hover img {
      transform: scale(1.03);
    }
  }

  &__text {
    margin-top: 1.2rem;
    min-height: 4.5rem;
  }

  /* ===== NAV ===== */
  &__nav {
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    transform: translateY(-50%);
    display: flex;
    justify-content: space-between;
    pointer-events: none;

    &-btn {
      pointer-events: auto;
      width: 44px;
      height: 44px;
      border-radius: 50%;
      border: 1px solid v.$azul;
      background-color: v.$blanco;
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;

      &-icon {
        width: 22px;
        height: 22px;
      }

      &:hover {
        background: v.$azul;
        color: v.$blanco;
      }

      &.prev {
        transform: translateX(-60%);
      }

      &.next {
        transform: translateX(60%);
      }
    }
  }
}

/* ======================================================
   üîç ZOOM IMAGEN
====================================================== */

.img-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.85);
  display: none;
  align-items: center;
  justify-content: center;
  z-index: 9999;

  &.is-visible {
    display: flex;
  }

  &__img {
    max-width: 90%;
    max-height: 90%;
    border-radius: 1rem;
    cursor: zoom-out;

    // üî• Mejora la nitidez
    image-rendering: auto;
    backface-visibility: hidden;
    transform: translateZ(0);

    // Evita reescalado raro
    object-fit: contain;
  }
}